const maskFormNumber = (selecter)  => {
    let leng = document.querySelector('html'),
    englBtns = document.querySelectorAll('#langvich'),
    ruspan = document.querySelector('#spanRu'),
    enpan = document.querySelector('#spanEUG');
   
    function fCurPosEnd(elem) {
        let sVal = elem.value.replace(/\D+/gi, '').slice(1);
        sVal = sVal + '__________'.slice(sVal.length);
        let sNumb = sVal.match(/(.{3})(.{3})(.{2})(.{2})/);
        if (ruspan.classList.contains('languichActive')) {
            elem.value = '+7(' + sNumb[1] + ')'+'-' + sNumb[2] + '-' + sNumb[3] + '-' + sNumb[4];
            // return elem;
        } else if (enpan.classList.contains('languichActive')){
            elem.value = '+1(' + sNumb[1] + ')'+'-' + sNumb[2] + '-' + sNumb[3]  + sNumb[4];
            // return  elem;
        }  
        elem.focus();
        elem.selectionStart = elem.selectionEnd = (elem.value.search(/[)-]*_/gi) < 0) ?
          elem.value.length :
          elem.value.search(/[)-]*_/gi);
      }

      LesenerInput();
      function LesenerInput() {
          let inputs = document.querySelectorAll(selecter);
          inputs.forEach(input => {
          
              input.addEventListener('input', function() {fCurPosEnd(this);});
                input.addEventListener('click', function() {fCurPosEnd(this);});
                input.addEventListener('keyup', function() {fCurPosEnd(this);
                });
                fCurPosEnd(input);
          });
      }

    englBtns.forEach(btn => {
        btn.addEventListener('click', ()=> {
            LesenerInput();
        });
    });
    

};      
export default maskFormNumber;