const scrollElement = () => {

  let  links = document.querySelectorAll('[href^="#"]'), // '[href^="#"]'        #
  speed = 0.3;

  links.forEach(item => {
      item.addEventListener('click', function (event) {
         event.preventDefault();
         
         let widhtTop = document.documentElement.scrollTop, 
             hash = this.hash,
             toBlock = document.querySelector(hash).getBoundingClientRect().top,
             start = null; 
             requestAnimationFrame(step);

             function step(time) {
              if(start === null){
                  start = time; 
              }

              let proggres = time -start,
                  r = (toBlock < 0 ? Math.max(widhtTop - proggres/speed, widhtTop + toBlock) : Math.min(widhtTop + proggres/speed, widhtTop + toBlock));
             document.documentElement.scrollTo(0, r);
             if(r != widhtTop + toBlock){
              requestAnimationFrame(step);
             } else {
              location.hash = hash;
             }
              }
      });
  });

};

export default scrollElement;