function clousModal(standart = true,time = 520) {
    const modal = document.querySelector('.modals_forms'),
    modalForm = document.querySelector('.modals_form');
    if(standart){
        document.body.style.overflow = '';
        modalForm.classList.remove('visible');
        setTimeout(() => {
          modal.classList.remove('showModal');
          modal.classList.add('hideModal');
        }, time);
    }else{
        document.body.style.overflow = '';
        modalForm.classList.remove('visible');
        setTimeout(() => {
            modalForm.classList.remove('showModal');
            modalForm.classList.add('hideModal');
        }, time);
    }
    
  }

  function openModal(standart = true,time = 300) {
    const modal = document.querySelector('.modals_forms'),
    modalForm = document.querySelector('.modals_form');
    if (standart) {
        modal.classList.add('showModal');
        modal.classList.remove('hideModal');
        document.body.style.overflow = 'hidden';
        setTimeout(() => {
          modalForm.classList.add('visible');
        }, time);
    } else {
        modalForm.classList.add('showModal');
        modalForm.classList.remove('hideModal');
        document.body.style.overflow = 'hidden';
        setTimeout(() => {
          modalForm.classList.add('visible');
        }, time);
    }
   
  }

  function createElement(select= '.modals_forms',ower = true) {
    const element = document.createElement('div');
    element.classList.add('data-atribut');
    element.style.zIndex = '300';
    if(ower== true){document.body.style.overflow = 'hidden';}else{}
   
    element.innerHTML = `
      <div class="modals_forms showModal"  >
          <div class="loader">
              <div class="inner one"></div>
              <div class="inner two"></div>
              <div class="inner three"></div>
          </div>
      </div>
       `;
       document.querySelector(select).appendChild(element);
       
  
  }
