function deleteElement (select,timeout, owerf = false) {
  setTimeout(() => {
   let i = document.querySelectorAll(select);
   i.forEach(item => {
      item.remove();
      if (owerf == true) {
        document.body.style.overflow = '';
      }

   });
  }, timeout);
}
