const formModal = () => {
    const modal = document.querySelector('.modals_forms'),
          modalForm = document.querySelector('.modals_form'),
          btn = document.querySelector('#bottenOpenForm'),
          btnCloouse = document.querySelector('.clouse__slide_form'),
         btnMobilClous =document.querySelector ('.mobile-close');

          btn.addEventListener('click', ()=> {
            openModal();
          });
          // clouse modal
          try {
            modal.addEventListener('click', (e)=> {
                if(e.target === modal) {
                  clousModalNau();
                }
            });
          } catch (error) {}
          btnMobilClous.addEventListener('click', ()=> {
            clousModal();
          });
          btnCloouse.addEventListener('click', ()=> {
            clousModalNau();
          });
          btnMobilClous.addEventListener('swiped-down', function(e) {
            clousModal();
          });

          function clousModalNau() {
            modal.classList.remove('showModal');
            modal.classList.add('hideModal');
            modalForm.classList.remove('visible');
            document.body.style.overflow = '';
          }
          function clousModal() {
            document.body.style.overflow = '';
            modalForm.classList.remove('visible');
            setTimeout(() => {
              modal.classList.remove('showModal');
              modal.classList.add('hideModal');
            }, 520);
          }

          function openModal() {
            modal.classList.add('showModal');
            modal.classList.remove('hideModal');
            document.body.style.overflow = 'hidden';
            setTimeout(() => {
              modalForm.classList.add('visible');
            }, 300);
          }
};

export default formModal;