import { createElement,  deleteElement} from "../services/modalservis";

 const trainslet = () => {
        let itext = require('../services/db.json'),
            btns = document.querySelectorAll('#langvich');
            
        const enBtn = document.querySelector('#spanEUG'),
              enBtnMini= document.querySelector('#spanEumini'),
              ruBtn= document.querySelector('#spanRu'),
              htmlAtt = document.querySelector('html'),
              htmlAttreb = document.querySelector('html').getAttribute('lang'),
              ruBtnMini= document.querySelector('#spanRuMini'),
              targetEl = document.querySelector('html');



        function callback(multitams) {
            console.log(multitams);
        }
        let observer  = new MutationObserver(mutationRecords => {
            console.log(mutationRecords);
            
        });

        observer.observe(targetEl, {
            attributes: true,
        });
        // optionLang();


        if (localStorage.getItem('index') == 0) {
            sovmet (0);
        } else if(localStorage.getItem('index') == 1){
            sovmet (1);
        } 

        /*buton actived */
        btns.forEach(btn => {
            btn.addEventListener('click', ()=> {
                // toggalVisualName();
                optionLang ();
                createElement('.gridmenu',false);
                createElement('.hamburger',false);
                deleteElement('.data-atribut', 500, true);
            try {
                removeElem('#spanRuMini');
                removeElem('#spanRu');
               
            } catch (error) {}
                });
            });

            function optionLang () {
                


                if (localStorage.getItem('index') == 0 || localStorage.getItem('index') == 1) {

                    if (localStorage.getItem('index') == 0) {
                            sovmet (1);
                        } else if(localStorage.getItem('index') == 1){
                            sovmet (0);
                        } 
                    
                } else {
                switch (htmlAtt.getAttribute('lang')) {
                    case 'en':
                        htmlAtt.setAttribute('lang', 'ru');
                        sovmet ();
                        break;
                    case 'ru':
                        htmlAtt.setAttribute('lang', 'en');
                            sovmet (1);
                        break;
                    default:
                        htmlAtt.setAttribute('lang', 'en');
                        sovmet (1); 
                        break;
                    }
                }
        
            }
            function removeElem(params) {
                const i = document.querySelector(params);
                      i.classList.remove('activePulsLangRu');
            }

            function sovmet (index=0) {
                for ( let key in itext.styles[index]){
                    for (let i = 0; i < 24; i++) {
                        const element = document.querySelector(`[data-translete="${i}"]`);
                        if(i == key){        
                        element.innerHTML = itext.styles[index][key];                            
                        }    
                    }
                }
           
        
                let  arr;
                if(index == 0){
                   arr = [' *','','Email *',''];
                } else {
                    arr  = ['Name *','Phone','Email *','your message'];
                }
                for (let i = 0; i < 4; i++) {
                    const elemAttr = document.querySelector(`[data-form="${i}"]`);     
                            elemAttr.setAttribute('placeholder', arr[i]);                            
                    }

                if (index == 0) {
                    localStorage.removeItem('index');
                    localStorage.setItem('index', 0);
                    htmlAtt.setAttribute('lang', 'ru');
                } else if(index == 1){
                    localStorage.removeItem('index');
                    localStorage.setItem('index', 1);
                    htmlAtt.setAttribute('lang', 'en');
                }

                if (index == 0 || htmlAttreb == 'ru') {
                    toggalVisualName(enBtn, enBtnMini, ruBtn, ruBtnMini);
                    ruBtn.classList.add('languichActive');
                    ruBtnMini.classList.add('languichActiveMini');
                } else if(index == 1 || htmlAttreb == 'en'){
                    toggalVisualName(enBtn, enBtnMini, ruBtn, ruBtnMini);
                    enBtn.classList.add('languichActive');
                    enBtnMini.classList.add('languichActiveMini');
                } else {
                    toggalVisualName(enBtn, enBtnMini, ruBtn, ruBtnMini);
                    enBtn.classList.add('languichActive');
                    enBtnMini.classList.add('languichActiveMini');
                }
    }



          

    function toggalVisualName(en, enMini, ru, ruMini) {
        en.classList.remove('languichActive', 'languichPassive');
        enMini.classList.remove('languichActiveMini', 'languichPassivemini');
        ru.classList.remove('languichActive', 'languichPassive');
        ruMini.classList.remove('languichActiveMini', 'languichPassivemini');
            }
 };

 export default trainslet;
