const userLocation = ()=> {
    fetch("https://ipinfo.io/json?token=005ace8f8bf7bd").then(
        (response) => {
            let res;
            if (response.status == 200 ) {
                res = response.json();  
              
                return res;
            } 
            return res;

        }).then(
        (jsonResponse) => {
        let leng = document.querySelector('html');

        if (jsonResponse.country == 'RU' && leng.getAttribute('lang') == 'en') {
            console.log('russia');
            cretePulsActiv('.span_3');
            cretePulsActiv('#spanRuMini', 11);
            cretePulsActiv('#spanRu', 11);
        } else {
            console.log('usa');
        }
        }
      ).catch(error=> {
      });
}; 
        function cretePulsActiv(selector, whitSl = null) {
            const i = document.querySelector(selector);
          const element = document.createElement('div');

         if(whitSl){
            i.classList.add('activePulsLangRu');
            
            setTimeout(() => {
                i.classList.remove('activePulsLangRu');
            }, 35000);
         } else {
            element.classList.add('activePulsLangveg');  
            i.append(element);
            setTimeout(() => {
                element.remove();
            }, 35000);
         }
        }
export default userLocation ;