import {clousModal, createElement, createGoodeSer, createBadSer, errorSeeUserIMG, deleteElement} from "../services/modalservis";

const form = () => {
    const formss = document.forms[0],
    htmlAttreb = document.querySelector('html'),
          btnTogg = document.querySelector('.togallMod');
   

            const clearInputs = () => {
                formss.forEach(item => {
                    item.value = '';
                });
            };
            //fetch
            const postData = async (url, data) =>  {
                let res = await fetch(url, {
                    method: 'POST',
                    body: JSON.stringify({data}),
                    
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                    },
                });
                if(!res.ok){ //     200      
                    throw Error(`could  not fetch ${url}, status: ${res.status}`); //   
                }
         
                return  await res;   
            };
            // spere toggel on/off gateway
            let url= 'https://jsonplaceholder.typicode.com/posts';

            btnTogg.addEventListener('click', () => {
                if(!btnTogg.classList.contains('active')) {
                    btnTogg.classList.add('active');
                   
                   return url= 'https:// jsonplaceholder.typicode.com/posts';
                }else {
                    btnTogg.classList.remove('active');
                    return url= 'https://jsonplaceholder.typicode.com/posts';
                }
            });
        
            formss.addEventListener('submit',(e) => {
                e.preventDefault();
              
                const formData = new FormData(formss);
            
              let arrey = {};
        
                function corsa () {
                    const name = formss[0],
                    phone = formss[1],
                    email = formss[2],
                    userText = formss[3],
                    submit = formss[4];
              
                    arrey = {
                        userName: name.value,
                        phone: phone.value,
                        email: email.value, 
                        userText: userText.value,
                        submit: submit
                    };
                
                    return arrey;
                    }
                if (formData.entries) {
                 
                } else {
             
                    corsa (); 
                }
            
                  let  value = formData.entries ? Object.fromEntries(formData.entries()): arrey ;
    
           
                  let  {email, phone, userName, userText} = value;
                // validtion user text 


             console.log(value);
             Countin(); 
            //  entries
             function Countin() {
                let masArrrt = Object.keys(value);
           
                const whiteList = ['userName', 'phone', 'email', 'userText', 'submit', 'phone' , 'userName'],
                      t = ['userName', 'phone', 'email', 'userText', 'submit'];

                let k = true;
                const shortNames = masArrrt.filter(function (name, i) {
                  
                    if (name == t[i]){
                        let j = name ;
                    } else {
                    
                        return k = false;
                    }
                });
            
                if (k == false) {
                    setTimeout(() => {
                        location.reload();
                       }, 6000);
             
                }

             }


         
                if (userName.length <= 0 || userName.length >= 51 || email.length <= 0 || email.length >= 257) {
                    //user entered incorrected
                  
                  
                  
                    if (userName.length <= 0 || userName.length >= 51 ) {

                        if (htmlAttreb.getAttribute('lang') == 'en') {
                            crateMasssegeNoValedText('name="userName"','Acceptable length from 1 to 50','art');
                        } else if(htmlAttreb.getAttribute('lang') == 'ru'){
                            crateMasssegeNoValedText('name="userName"','   1  50','art');
                        }
                    } 
                    if( email.length <= 0 || email.length >= 257){
                        if (htmlAttreb.getAttribute('lang') == 'en') {
                            crateMasssegeNoValedText('name="email"','Acceptable length from 1 to 256','eart');
                        } else if(htmlAttreb.getAttribute('lang') == 'ru') { 
                            crateMasssegeNoValedText('name="email"','   1  256','eart');
                        }
                    }
                } else {
                    //user entered corrected
                    clousModal(false, 400);//2 operating modes
                    createElement(); // add spiner 
                    setTimeout(() => {
                        try {
                            const i = document.querySelector('.data-atribut');
                            if(i){
                            setTimeout(() => {
                                deleteElement('.data-atribut', 10);
                                createBadSer();
                                deleteElement('.data-svg-badSer', 2200);
                                remove(2500);
                                errorSeeUserIMG();
                                setTimeout(() => {
                                    document.querySelector('.modals_form').classList.add('visible');
                                }, 100);
                               }, 1500); 
                              
                        }
                      } catch (error) {}
                        
                    },25000);

                    try {

                        const j = document.querySelectorAll('.allRemoveClasse');
                        j.forEach(item=>{
                            item.remove();
                        });
                        
                    } catch (error) {}
     
                 postData(url, value)
                 .then((response) =>{
                     console.log(response);

                     if (!response.ok) {
                        throw Error(response.statusText);
                      }
                     setTimeout(() => {
                         deleteElement('.data-atribut', 10); //delete spiner
                         createGoodeSer(); // add  :)
                         deleteElement('.data-svg', 2000); //delete 
                         remove(1100);
                         setTimeout(() => {
                             document.querySelector('.modals_form').classList.add('visible');
                         }, 1500);
                     }, 1500); //spiner show
                     let res = response.json();
                     return res;
                 })
                 .then((json) => console.log(json))
                 .catch((error) => {
                    setTimeout(() => {
                     deleteElement('.data-atribut', 10);
                     createBadSer();
                     deleteElement('.data-svg-badSer', 2200);
                     remove(2500);
                     errorSeeUserIMG();
                     setTimeout(() => {
                         document.querySelector('.modals_form').classList.add('visible');
                     }, 100);
                     console.error(`  ${error}`);
                    }, 1500); 
                   
                 }).finally(() => {
                     clearInputs();
                     setTimeout(() => {
                         document.querySelector('.modals_form').classList.add('visible');
                     }, 6400);
                 });
                }

               
      

        });
      function remove(timeout=1300){
        setTimeout(() => {
            document.querySelector('.modals_form').classList.remove('hideModal'); 
        }, timeout);
      }
    
        function crateMasssegeNoValedText(nameInput,text,atribut) {
            let mssege = document.querySelector(`.modal__form [${nameInput}]`),
                NevCreateElem = document.querySelector(`.data-bad-${atribut}`),
                btns = document.querySelectorAll('#langvich');
                
                btns.forEach(btn => {btn.addEventListener('click',()=>{
                   try {
                    let NevCreate = document.querySelector(`.data-bad-${atribut}`);
                    NevCreate.remove();
                   } catch (error) {}
               
                });
            });

            if(NevCreateElem){
                NevCreateElem.remove();
             
              const element = document.createElement('div');
              element.classList.add(`data-bad-${atribut}`, 'allRemoveClasse');
              element.style.cssText= "zIndex=33; color: red; position: relative; top: -22px;";
              element.innerText = `${text}`;
              mssege.after(element);
            } else{
                const element = document.createElement('div');
                element.classList.add(`data-bad-${atribut}`, 'allRemoveClasse');
                element.style.cssText= "zIndex=33; color: red; position: relative; top: -22px;";
                element.innerText = `${text}`;
                mssege.after(element);
            }
           
          
        }
 
};

export default form;
