import {createSwipeBord} from '../services/modalservis'; 
const imgBig = () => {
             
    const modal = document.querySelector('#myModal'),
        img = document.querySelectorAll('.photoGrid'),
        modalImg = document.querySelector('#img01'),
        captionText = document.querySelector('#caption'),
        skilsIcon = document.querySelectorAll('.photo'),
        html = document.querySelector('html'),
        itext = require('../services/db.json'),
        span = document.querySelector('.clouse'); 

        modal.classList.add('hideModal');

            /*clous modall x */ 
        try {
        span.addEventListener('click', clouseModal); 
        } catch (error) {} 
        /*clous modall click backgraund */
        modal.addEventListener('click', (e)=> {
            if(e.target === modal) {
                clouseModal();
            }
        });
        /*clous modall esc */
        document.addEventListener('keydown', (e)=> {
            if (e.code === 'Escape' && modal.classList.contains('showModal')) {
                clouseModal();
            }
        });

        let count; 
            /*open modall */
        img.forEach((item,i) => {
            item.addEventListener('click', (e)=>{
                openImgModal(e, item);
        
                
                count = i;
                var w = parseInt(window.innerWidth); 
                if (localStorage.getItem('imgBigLS') != 1 && w <= 576) {
                    setTimeout(() => {
                        createSwipeBord('#myModal',5000, 'imgBigLS');
                    }, 1000);
                }
                document.addEventListener('keydown', (e)=> {
                    if (e.code === 'ArrowRight') {
                        slidDecrement();
                    }
                });
                document.addEventListener('keydown', (e)=> {
                    if (e.code === 'ArrowLeft') {
                        slideIncreent();
                    }
                });
                modalImg.addEventListener('swiped-left', slideIncreent);
                modalImg.addEventListener('swiped-right', slidDecrement);
        
            });
        });

        /* ---------------------------------------------------------*/
        skilsIcon.forEach((item,i) => {
            item.addEventListener('click', (e)=>{
        
                console.log( i);
                openImgModal(e, item);
                rtyu(1, i, item);
              
                modalImg.classList.add('modal_cont_skils');
            
            });
        });

        function slideIncreent() {
            count++;
            if (count > img.length -1) {
                count = 0; 
            }
            slide();
        }
        function slidDecrement() {
            count--;
            if (count < 0) {
                count = img.length -1; 
            }
            slide();
        }
        function slide() {
        
            modalImg.src =  img[count].getAttribute('src');
            modalImg.alt =  img[count].getAttribute('alt');
            captionText.textContent = img[count].parentNode.nextElementSibling.textContent ;
            
        }
        function openImgModal(e,item) {
            let target = e.target;
            
            modal.classList.remove('hideModal');
            modal.classList.add('showModal');
            document.body.style.overflow = 'hidden';

            modalImg.src =  target.getAttribute('src');
            modalImg.alt =  target.getAttribute('alt');

            captionText.textContent = item.parentNode.nextElementSibling.textContent ;
        }
        function clouseModal() {
            modal.classList.remove('showModal');
            modal.classList.add('hideModal');
            modalImg.classList.remove('modal_cont_skils');
            modalImg.className = ''; //  
            let p = modalImg.classList;
            console.log(p);  
            console.log(p.length);
            /*    ,            
              ,   ,     ,      
            for (const iterator of p)  {
                
                // let p = modalImg.classList;
                        // console.log(iterator);
                    //    if(p !== '') {
                        console.log(' ');
                           console.log(modalImg.classList);
                           modalImg.classList.remove(`${iterator}`);
                           console.log(modalImg.classList);
                        // }else {
                            console.log('');
                        // }
                }
                // console.log(modalImg.classList);
            // console.log(modalImg.classList);
          */
            modalImg.classList.add('modal_content');
            document.body.style.overflow = '';
        }
        function rtyu(index = 3, i, item)  {
            if (html.getAttribute('lang') == 'en') {
                index = 2;
            } else if(html.getAttribute('lang') == 'ru') {
                index = 3;
            }
            let p = item.classList;
            modalImg.classList.add(p[1]);
            for ( let key in itext.styles[index]){
                if(i == key){        
                    
                    let elem = document.createElement('div');
                    
                    elem.innerHTML = `<div id="caption">${itext.styles[index][key]}</div>`;

                    captionText.append(elem);
                }    
                
            }
        }
        
};

export default imgBig;